import {isBright} from "@/common/util";

const axios = require("axios");

class PlayerBasic {
  constructor(player) {
    ['id', 'name', 'nationality', 'homeLocation', 'headshot', 'fullName', 'role', 'number'].forEach(key =>
      this[key] = player[key]
    );

    this.fullName = this.fullName ? this.fullName : `${player.givenName} ${player.familyName}`;
    this.nationality = player.nationality;
    this.role = this.role === undefined && player.attributes ? player.attributes.role : this.role;
    this.number = this.number === undefined && player.attributes ? player.attributes.player_number : this.number;

  }
}

class Player {
  constructor(data) {
    this.basic = new PlayerBasic(data.player);
    this.team = new Team(data.team.data, {showPlayers: false});

    this.heroes = [];
    if (data.stats) {
      data.stats.heroes.forEach(hero => {
        let stats = hero.stats;
        stats.hero = hero.name;
        this.heroes.push(stats)
      });
      this.all = [];
      data.stats.all.type = "数据";
      this.all.push(data.stats.all);
      data.statRanks.type = "排名";
      this.all.push(data.statRanks);
    }
  }

  static getHeros(data) {
    if (data.stats) {
      const heroes = {};
      data.stats.heroes.forEach(hero => {
        heroes[hero.name] = {};
        hero.stats.forEach(metric => {
          heroes[hero.name][metric.name] = {value: metric.value}
        })
      });
      heroes['all'] = {};
      data.stats.stats.forEach(metric => {
        heroes['all'][metric.name] = {value: metric.value}
      });
      return heroes
    }
    return {}

  }
}

class Team {
  constructor(team, {showPlayers = true} = {}) {
    ['id', 'name', 'abbreviatedName', 'logo', 'icon', 'location', 'colors', 'matches', 'placement', 'records', 'schedule'].forEach(
      key => this[key] = team[key]);
    this.players = showPlayers ? team.players.map(x => new PlayerBasic(x)) : undefined;
    this.league = team.league;
    this.stages = team.stages;
    if (team.content) {
      this.primaryColor = team.content.colors.find(x => x.usage === 'primary').color.color;
      const icon = team.content.icons.find(x => x.usage === 'alt') || team.content.icons.find(x => x.usage === 'altDark')
    }

  }
}

class TeamBasic {
  constructor(team, {showPlayers = true} = {}) {
    ['id', 'name', 'abbreviatedName', 'logo', 'location', 'colors',].forEach(key => this[key] = team[key]);
    this.players = {};
    if (showPlayers) {
      team.players.map(x => new PlayerBasic(x)).forEach(player => {
        this.players[player.id] = player
      })
    }
    this.primaryColor = team.colors.primary.color;

    if (isBright(team.colors.primary.color.slice(1))) {
      this.fontColor = 'black';
    } else {
      this.fontColor = 'white';
    }
    this.img = team.logo.alt ? team.logo.alt.svg : team.logo.main.svg;
  }
}

class GameBrief {
  constructor(game) {
    this.id = game.id;
    this.state = game.state;
    this.points = game.points;
    if (game.attributes) {
      this.mapId = game.attributes.mapGuid
    }
  }
}

class Match {
  constructor(match, {showPlayers = true} = {}) {
    this.id = match.id;
    this.competitors = match.competitors.map(x => new Team(x, {showPlayers: showPlayers}));
    this.startDate = match.startDate;
    this.endDate = match.endDate;
    this.state = match.state;
    this.scores = match.scores;
    this.winner = match.winner;
    this.startDay = new Date(match.startDateTS).toLocaleDateString()
  }
}

class MatchDetail {
  constructor(match) {
    this.id = match.id;
    this.competitors = match.competitors.map(x => new Team(x, {showPlayers: false}))
    this.startDate = match.actualStartDate ? match.actualStartDate : match.startDate;
    this.endDate = match.actualEndDate ? match.actualEndDate : match.endDate;
    this.state = match.state;
    this.scores = match.scores;
    this.rule = match.conclusionStrategy === 'MINIMUM' ?
      match.meta.strings["owl.matches.game.type.minimum"].replace("{0}", match.conclusionValue) :
      match.meta.strings["owl.matches.game.type.best-of"].replace("{0}", match.conclusionValue);
    this.games = match.games.map(x => new GameBrief(x))
  }

}

class Week {
  constructor(data) {
    this.id = data.id;
    this.startDate = data.startDate;
    this.endDate = data.endDate;

    const matches = data.matches.map(x => new Match(x, {showPlayers: false}));
    this.days = [];
    let day = {};
    matches.forEach(match => {
      if (day.startDay === match.startDay)
        day.matches.push(match);
      else {
        if (day.startDay !== undefined) {
          this.days.push(day);
          day = {}
        }
        day.startDay = match.startDay;
        day.matches = [match]
      }
    });
    if (Object.keys(day).length > 0)
      this.days.push(day);
  }
}

class Stage {
  constructor(data) {
    this.id = data.id;
    this.name = data.name;
    this.slug = data.slug;
    this.matches = data.matches.map(x => new Match(x, {showPlayers: false}))
    this.weeks = data.weeks ? data.weeks.map(x => new Week(x)) : [];
  }
}

class OWL {
  constructor({inChina = false}) {
    this.locale = inChina === true ? 'zh-cn' : 'en';
    this.api = axios.create({
      crossDomain: true,
      crossOrigin: true,
      baseURL: 'https://api.overwatchleague.com/',

      //baseURL: inChina === true ? 'https://api.overwatchleague.cn/' : 'https://api.overwatchleague.com/',
      params: {
        locale: this.locale
      }
    })
    ;
  }

  static get STAGE() {
    return {
      REGULAR: 'regular_season',
      POST: 'postseason'
    }
  }

  async getPlayer(id, {season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/players/${id}`, {
        params: {
          season: season,
          stage_id: stage,
          expand: 'stats,stat.ranks,team,team.matches.recent,similarPlayers'
        }
      })
        .then(response => resolve(new Player(response.data.data)))
        .catch(err => reject(err))

    })

  }

  async allMaps() {
    return new Promise((resolve, reject) => {
      this.api.get(`/maps`,)
        .then(response => {
          const maps = {};
          response.data.forEach(map => {
            const m = {}
            m.name = this.locale === 'zh-cn' ? map.name['zh_CN'] : map.name['en_US']
            m.thumbnail = map.thumbnail;
            m.icon = map.icon;
            m.type = map.gameModes ? map.gameModes[0].Name : undefined;
            maps[map.guid] = m
          });
          resolve(maps)
        })
        .catch(err => reject(err))
    })
  }

  async gameDetail(matchId, mapId) {
    return new Promise((resolve, reject) => {
      this.api.get(`/stats/matches/${matchId}/maps/${mapId}`)
        .then(response => resolve(response.data))
        .catch(err => {
          resolve(err)
        });
    })
  }

  async getTeam(id) {
    return new Promise((resolve, reject) => {
      this.api.get(`/v2/teams/${id}`, {
        params: {
          expand: "schedule,matches.recent"
        }
      })
        .then(response => resolve(new Team(response.data.data)))
        .catch(err => reject(err))
    })
  }

  async getAllTeams({season = 2019} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get('v2/standings', {
        params: {
          season: season
        }
      })
        .then(response => resolve({
          team: response.data.data.map(x => new Team(x, {showPlayers: false})),
          meta: response.data.meta
        }))
        .catch(err => reject(err))
    })
  }

  async allTeams() {
    return new Promise((resolve, reject) => {
      this.api.get(`/v2/teams`)
        .then(response => {
          const teams = {};
          response.data.data.forEach(team => {
            teams[team.id] = new TeamBasic(team, {showPlayers: true});
          });
          resolve(teams)
        })
        .catch(err => reject(err))
    })
  }

  async allPlayers({season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/stats/players`, {
        params: {
          season: season,
          stage_id: stage,
        }
      })
        .then(response => resolve(response.data.data))
        .catch(err => reject(err))
    })
  }


  async schedule({season = 2019} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get('/schedule', {
        params: {
          season: season
        }
      })
        .then(response => resolve({
          stages: response.data.data.stages.map(x => new Stage(x)).filter(x => ['playoffs', 'stage1', 'stage2', 'stage3', 'stage4'].includes(x.slug)),
          meta: response.data.meta
        }))
        .catch(err => reject(err))
    })
  }

  async matchDetail(id) {
    return new Promise((resolve, reject) => {
      this.api.get(`/match/${id}`, {
        params: {
          expand: 'team.content'
        }
      })
        .then(response => {
          resolve(new MatchDetail(response.data));
        })
        .catch(err => reject(err))
    })

  }

  async allPlayersDetailed({season = 2019} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get('/players', {
        params: {
          expand: 'stats',
          season: season
        }
      })
        .then(response => {
          const r = {};
          const d = {};
          response.data.forEach(x => {
            const h = Player.getHeros(x)
            r[x.id] = h;
            Object.keys(h).forEach(hero => {
              if (d[hero] === undefined)
                d[hero] = {};
              Object.keys(h[hero]).forEach(metric => {
                if (d[hero][metric] === undefined)
                  d[hero][metric] = {};
                d[hero][metric][x.id] = h[hero][metric]
              })
            })
          });
          Object.keys(d).forEach(hero => {
            Object.keys(d[hero]).forEach(metric => {
              Object
                .keys(d[hero][metric])
                .sort((id1, id2) => d[hero][metric][id2].value - d[hero][metric][id1].value)
                .forEach((id, i) => {
                  d[hero][metric][id].rank = i + 1
                });
            })
          });
          resolve({players: r, ranks: d});
        })
        .catch(err => reject(err))
    })
  }

}

export default OWL;
