<template>
  <div id="app">
    <Header></Header>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
  import Header from "@/components/Header";

  export default {
    name: 'App',
    components: {Header},
    mounted() {
      this.$store.dispatch('setTeams');
      const that = this;
      window.onresize = function temp() {
        that.$store.dispatch('updateWindowParam', {width: window.screen.availWidth, height: window.screen.availHeight});
      };
    }
  }
</script>

<style>
  #app {
    font-family: "Industry", Noto Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    max-width: 1060px;
    margin: 0 auto;

  }

  li, input {
    font-family: "Industry", Noto Sans, Helvetica Neue, Helvetica, Arial, sans-serif;

  }

  @font-face {
    font-family: 'Industry';
    src: url('assets/industry-bold-webfont.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
  }

  @font-face {
    font-family: 'Industry';
    src: url('assets/industry-medium-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }


  #app .el-table th.is-leaf {
    border-bottom: 0.1rem solid #ebeef5;
  }

  #app .el-tabs__nav-scroll {
    overflow-x: auto;
  }

  #app .el-tabs__nav-scroll::-webkit-scrollbar, #app .el-tabs__nav-prev, #app .el-tabs__nav-next {
    display: none;
  }

  #app .el-tabs__nav-wrap.is-scrollable {
    padding: 0;
  }

  a {
    text-decoration: none;
    color: black
  }

  a:hover {
    color: #ff8900
  }

  #app .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    border: none;
    margin: 0
  }

  .tab-outside .el-tabs__content {
    padding: 0 !important;
  }

  .tab-inside .el-tabs__content {
    padding: 20px !important;
  }

  .tab-inside, .el-tabs--border-card > .el-tabs__header, .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    border: none;
  }


  .el-tabs__nav-wrap {
    margin: 0 !important;
  }

  .tab-outside .el-tabs__header {
    background-color: #f1f1f1 !important;
    border-bottom: 3px solid #ff8900 !important;
  }


  .tab-outside .el-tabs__item.is-active, .tab-outside .el-tabs__item:hover {
    color: white !important;
    background-color: #ff8900 !important;
    font-weight: 700 !important;
  }

  .tab-inside .el-tabs__header {
    background-color: #333333 !important;
    border: none !important;
  }


  .tab-inside .el-tabs__item.is-active, .tab-inside .el-tabs__item:hover {
    color: white !important;
    background-color: #444444 !important;
    font-weight: 700 !important;
  }

  .el-tabs--border-card {
    border: none !important;
  }

  .el-table thead {
    color: white;
  }

  .el-table th {
    background-color: #444444;
  }

  .el-table__column-filter-trigger i {
    color: white;
    font-weight: 700;
  }

  .el-table th > .cell {
    line-height: 40px;
  }

  .el-table__header-wrapper {
    background-color: #444444;
  }

  .el-table th {
    border: none !important;
  }

  .hero-portrait-image-mask {
    width: 60px;
    height: 72px;
    background-color: rgba(51, 51, 51, .9);
    background-clip: padding-box;
    overflow: hidden;
    border-radius: 7px;
  }

  .hero-portrait-image {
    display: inline-block;
    width: calc(100% + 10px);
    height: 100%;
    background-size: auto 100%;
    background-position: 50% 50%;
    position: relative;

  }

  @media (max-width: 768px) {
    .el-table {
      font-size: 12px;
    }

    .el-table td, .el-table th {
      padding: 5px 0;
    }

    .hero-portrait-image-mask {
      width: 40px;
      height: 48px;
    }
  }

  .el-table--group::after, .el-table--border::after, .el-table::before {
    background-color: transparent;
  }

</style>



