<template>
  <div class="header">
    <div class="top-right">
      <div class="header-logo-small">
        <router-link to="/">
          <img :src="logoUrl" style=" width: 100%;
      max-width: 96px">
        </router-link>
      </div>
      <el-dropdown @command="chooseSeason" trigger="click">
        <span class="el-dropdown-link">
          {{season}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item :command="2019">2019</el-dropdown-item>
          <el-dropdown-item :command="2018">2018</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <div class="top-search">
        <el-autocomplete placeholder="选手" prefix-icon="el-icon-search" v-model="autocomplete" :trigger-on-focus="false"
                         :fetch-suggestions="querySearch" @select="handleSelect"
                         v-if="$route.name!=='Players'"></el-autocomplete>
        <el-input placeholder="选手" prefix-icon="el-icon-search" v-model="search"
                  @keyup.enter.native="handleEnterInput"
                  v-else></el-input>
      </div>
      <el-menu :default-active="activeIndex" mode="horizontal">
        <el-menu-item index="1">
          <router-link to="/players" class="nav-link">选手</router-link>
        </el-menu-item>
        <el-menu-item index="2">
          <router-link to="/teams" class="nav-link">战队</router-link>
        </el-menu-item>
        <el-menu-item index="3" class="nav-link">
          <router-link to="/" class="nav-link">比赛</router-link>
        </el-menu-item>
      </el-menu>
    </div>
  </div>

</template>

<script>
  export default {
    name: "Header",
    data() {
      return {
        autocomplete: undefined,
        logoUrl: process.env.NODE_ENV === 'development' ? "/static/logo.png" : "/owl/static/logo.png"
      }
    },
    methods: {
      handleSelect(player) {
        this.$router.push(`/player/${player.id}/${player.value}`)
      },
      querySearch(queryString, cb) {
        if (this.$store.getters.getPlayerBriefs(this.season) === undefined)
          this.$store.dispatch('setPlayersBrief', [this.season, false]).then(() => {
            this.doQuery(queryString.toLowerCase(), cb)
          });
        else
          this.doQuery(queryString.toLowerCase(), cb)
      },
      doQuery(queryString, cb) {
        cb(this.$store.getters.getPlayerBriefs(this.season).filter(x => x.name.toLowerCase().includes(queryString)).map(x => ({
          value: x.name,
          id: x.playerId
        })))
      },
      chooseSeason(season) {
        if (season !== this.season)
          this.$store.dispatch('updateSeason', season)

      },
      handleEnterInput() {
        if (this.$route.name !== 'Players')
          this.$router.push({
            name: 'Players',
            query: {
              player: this.search
            }
          })
      }
    },
    computed: {
      season() {
        return this.$store.getters.getSeason
      },
      activeIndex() {
        return this.$store.getters.getCurrentTabIndex
      },
      search: {
        get() {
          return this.$store.getters.getCurrentSearch;
        },
        set(value) {
          this.$store.dispatch('updateSearch', value)
        }
      }
    }
  }
</script>

<style>
  .el-autocomplete-suggestion li {
    padding: 0 .6rem;
    line-height: 1.5rem;
  }

  .header-logo-small {
    display: inline-block;
    flex: 1 1 auto;
    text-align: left;

  }

  .nav-link {
    display: block;
    height: 100%;
    padding: 0 20px;
  }

  .header .el-menu--horizontal > .el-menu-item {
    height: 42px;
    line-height: 40px;
    padding: 0;
    font-size: 16px;
    font-weight: 700;
  }

  .header .el-menu.el-menu--horizontal {
    border: none;
  }

  .header {
    height: 60px;
    box-sizing: border-box;
  }

  .header .top-right {
    display: flex;
    align-items: center;
  }

  .header .top-right .el-input {
    max-width: 9rem;
    margin: 0 10px;
  }

  .header .top-right .el-input__inner {
    border-radius: 20px;
    line-height: 30px;
    height: 30px;
    color: #757575;
    background-color: #f2f2f2;
  }

  .header .top-right .el-input__prefix {
    color: #757575;
  }

  .header .top-right .el-input.is-active .el-input__inner, .el-input__inner:focus {
    border-color: transparent;
  }

  .header .top-right .el-input__icon {
    line-height: 30px;

  }

  .top-search, .header .el-dropdown {
    flex-shrink: 0;

  }

  .header ul {
    display: flex;
  }

  @media (max-width: 768px) {
    .nav-link {
      padding: 0 5px;
    }

    .header .el-menu--horizontal > .el-menu-item {
      font-size: 14px;
      font-weight: 400;
    }

    .header .top-right .el-input {
      max-width: 5rem;

    }

  }
</style>



