import Vue from 'vue'
import Router from 'vue-router'
import RouterPrefetch from 'vue-router-prefetch'

const Players = () => import('@/components/Players');
const Player = () => import('@/components/Player');
const Teams = () => import('@/components/Teams/Teams');
const Matches = () => import('@/components/Matches/Matches');
const Team = () => import('@/components/Team/Team');
const Match = () => import('@/components/Match/Match');

Vue.use(Router);
Vue.use(RouterPrefetch);

export default new Router({
  mode: 'history',
  base: '/owl/',
  routes: [
    {
      path: '/players',
      name: 'Players',
      component: Players,
      meta: {
        title: "OWL Players"
      }
    },
    {
      path: '/player/:id/:name',
      name: 'Player',
      component: Player,
      meta: {
        title: "OWL Player"
      }
    },
    {
      path: '/teams/',
      name: 'Teams',
      component: Teams,
      meta: {
        title: "OWL Teams"
      }
    },
    {
      path: '/',
      name: 'Matches',
      component: Matches,
      meta: {
        title: "OWL Matches"
      }
    },
    {
      path: '/team/:id/:name',
      name: 'Team',
      component: Team,
      meta: {
        title: "OWL Team"
      }
    },
    {
      path: '/match/:id',
      name: 'Match',
      component: Match,
      meta: {
        title: "OWL Match"
      }
    },
  ]
})



